body {
  margin: 0 10vmin !important;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #121F45; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* NAVBAR  */

.brand-name{
  /* color: white; */
  color: black;
  font-family: 'Montserrat';
  font-size: 24px;
  padding-top: 100px;
  font-weight: bold;
  line-height: 80%;
  letter-spacing: 5px;
}


/* HOME PAGE */

#header{
  width: 100%;
  /* height: 180vh; */
  /* background-color: #121F45; */
  /* border: 1px solid red; */
  padding-bottom: 80px;
}

.intro h1{
  font-family: 'Montserrat';
  /* color: #CB2026; */
  color: black;
  font-weight: bold;
  font-size: 96px !important;
  line-height: 140px;
}

.intro{

  /* text-align: center; */
  margin-top: 120px !important;
  margin-bottom: 120px !important;
}

.photo{
  background-image: url('./images/center.jfif');
  background-position: center;
  background-color: brown;
  background-blend-mode: multiply;
  background-attachment: scroll;
  margin: 80px auto;
  width: 100%;
  height: 80vh;
}

/* ABOUT PAGE */

.max-width{

  max-width: 940px;
  margin: 0 auto;
}

p.large{

  font-weight: 600;
  font-family: 'Montserrat';
  /* color: white; */
  color: black;
  font-size: 46px;
  line-height: 140%;
  margin-bottom: 50px;
  margin-top: 120px;
  /* background-color: #CB2026;
  padding: 30px 30px; */
}

.max-width .small{
  /* color: white; */
  color: black;
  font-size: 25.6px;
  padding-bottom: 20px;
  line-height: 160%;
  font-family: 'Montserrat';
}

.contact{
  font-size: 24px;
  line-height: 180%;
  /* color: white; */
  color: black;
  font-family: 'Montserrat';
}

@media only screen and (max-width: 768px){
  .intro h1{
    font-size: 56px !important;
    line-height: 120%;
  }
  p.large{

    font-size: 38.4px;
  }
  .max-width .small{
    font-size: 22.4px;
  }
}

@media only screen and (max-width: 320px){
  .intro h1{
    font-size: 36px !important;
    line-height: 120%;
  }
  .photo {
    background-size: cover;
    background-repeat: no-repeat;
    height: 30vh;
}
  p.large{

    font-size: 30px;
  }
  .max-width .small{
    font-size: 20.4px;
  }

  .contact {
    font-size: 20.4px;
}
}